    addLineCompare(){
      //echartslegend
      var templist=[]
      var i=0

      while(i<this.checkListCompare.length){
        if(this.firstTimeValue !=null)
          templist[2*i]=this.firstTimeValue + this.checkListCompare[i]
        if(this.secondTimeValue !=null)
          templist[2*i+1]=this.secondTimeValue + this.checkListCompare[i]
        i=i+1
      }
      this.echartsOption.legend.data=templist

      //echartsselect,

      //selectedfalse
      i=0
      for(i=0;i<this.echartsOption.series.length;i++){
        var key = this.echartsOption.series[i].name
        this.echartsOption.legend.selected[key] = false

      }
    
      //true
      i=0
      for(i=0;i<templist.length;i++){
        var key = templist[i]
        this.echartsOption.legend.selected[key] = true
      }

      

