<!-- APP-->
<template>
  <div>
    <div>
      <!-- APP -->
      <div class="buttonBackground" id="btn_normal">
        <button type="text" @click="clickRect(1)"></button>
        <p></p>
      </div>
    </div>
    <!--  -->

    <div
      v-for="devOne in devDialogs"
      :key="devOne.devCode"
      class="multi-dialog"
      :id="devOne.box"
      :style="{ left: devOne.left, top: devOne.top }"
    >
      <div class="multi-dialog-title" :id="devOne.title">
        <span>{{ devOne.devCode }}</span>
        <button
          type="button"
          aria-label="Close"
          class="el-dialog__headerbtn"
          @click="closeDialog(devOne)"
        >
          <i class="el-dialog__close el-icon el-icon-close"></i>
        </button>
      </div>
      <div class="multi-dialog-content">
        <!-- <bpg-kzmb></bpg-kzmb> -->
        
      </div>
    </div>
  </div>
</template>
<style src="../assets/css/button.css">
</style>
<script>
export default {
  data() {
    return {
      devDialogs: [],
    };
  },
  methods: {
    // 
    clickRect(val) {
      // 
      let exist = false;
      this.devDialogs.forEach((element) => {
        if (val === element.devCode) {
          exist = true;
        }
      });
      if (!exist) {
        let devOne = {
          devCode: val,
          box: "box" + val,
          title: "title" + val,
          left: "20px",
          top: "20px",
        };
        this.devDialogs.push(devOne);
        this.$nextTick(() => {
          this.divMove(devOne.title, devOne.box);
        });
      }
    },
    // 
    closeDialog(devOne) {
      this.devDialogs.forEach(function (item, index, arr) {
        if (item.devCode === devOne.devCode) {
          arr.splice(index, 1);
        }
      });
    },
    // 
    divMove(titleId, boxId) {
      let title = document.getElementById(titleId); // 
      let box = document.getElementById(boxId); // 
      let divX = 0; // divx
      let divY = 0; // divy
      let isDrop = false; //  true false
      let self = this;
      // title
      title.onmousedown = function (e) {
        let el = e || window.event; // 
        divX = el.clientX - box.offsetLeft; // x
        divY = el.clientY - box.offsetTop; // y
        isDrop = true; // true
        document.onmousemove = function (e) {
          // 
          if (isDrop) {
            let el = e || window.event;
            let leftX = el.clientX - divX; // 
            let leftY = el.clientY - divY; // 
            //  
            let maxX = document.documentElement.clientWidth - box.offsetWidth; // -
            let maxY = document.documentElement.clientHeight - box.offsetHeight; // -
            // leftX < 0leftY < 00
            // leftX > maxXleftY > maxYmaxXmaxY
            leftX = Math.min(maxX, Math.max(0, leftX));
            leftY = Math.min(maxY, Math.max(0, leftY));
            box.style.left = leftX + "px";
            box.style.top = leftY + "px";
          }
        };
        document.onmouseup = function () {
          // divdivdiv
          self.devDialogs.forEach(function (item) {
            if (item.box === boxId) {
              item.left = box.style.left;
              item.top = box.style.top;
            }
          });
          isDrop = false;
          document.onmousemove = null;
          document.onmouseup = null;
        };
      };
    },
  },
};
</script>
<style scoped>
.just-click {
  cursor: pointer;
  width: 100px;
  height: 40px;
  position: fixed;
  background: white;
  color: black;
  line-height: 40px;
  text-align: center;
}
.multi-dialog {
  position: fixed;
  width: 580px;
  background: rgba(0, 93, 172, 0.75);
  box-shadow: 0px 0px 12px rgba(0, 186, 255, 0.5);
  top: 20px;
  left: 20px;
  z-index: 10;
  font-size: 14px;
}
.multi-dialog-title {
  padding: 20px;
  border: 1px solid rgba(0, 93, 172, 0.75);
  border-top: 2px solid rgba(127, 255, 255);
  cursor: move;
  font-size: 18px;
}
.multi-dialog-content {
  padding: 10px;
}
.buttonBackground button {
  background-image: url(../assets/icon/cityload.png);
}
.buttonBackground {
  left: 21rem;
  top: 8rem;
}
</style>

