      this.tableData6[10].name6 = "";
      this.tableData6[11].name6 = "1#";
      this.tableData6[12].name6 = "";
      this.tableData6[13].name6 = "1#";
      this.tableData6[14].name6 = "1#";
      this.tableData6[15].name6 = "2#";

      // while (i < this.TABLEdata.length) {
      while (i < 16) {
        this.tableData6[i].frequency6 = this.TABLEdata[i].frequencyDeviation;
        this.tableData6[i].A6 = this.TABLEdata[i].ia;
        this.tableData6[i].B6 = this.TABLEdata[i].ib;
        this.tableData6[i].C6 = this.TABLEdata[i].ic;
        this.tableData6[i].Uab6 = this.TABLEdata[i].uab;
        this.tableData6[i].Ubc6 = this.TABLEdata[i].ubc;
        this.tableData6[i].Uca6 = this.TABLEdata[i].uca;
        i = i + 1;
      }
    },
    //echart
    charge() {
      let i = 0;
      while (i < this.totaldata.length) {
        //
        var date = new Date(
          this.totaldata[i].dataTime * 1000 + 8 * 3600 * 1000
        );
        var daytime = date
          .toJSON()
          .substr(0, 19)
          .replace("T", " ")
          .replace(/-/g, ".");
        console.log(daytime);
        // console.log(this.totaldata.length)
        this.option1.xAxis.data[i] = daytime.substring(5, 10); //X
        this.option1.series[0].data[i] = this.totaldata[i].highKWh; //
        this.option1.series[1].data[i] = this.totaldata[i].lowKWh; //
        this.option1.series[2].data[i] = this.totaldata[i].midKWh; //
        this.option1.series[3].data[i] = this.totaldata[i].topKWh; //

        this.option2.xAxis.data[i] = daytime.substring(5, 10); //X
        this.option2.series[0].data[i] = this.totaldata[i].highCharge; //
        this.option2.series[1].data[i] = this.totaldata[i].lowCharge; //
        this.option2.series[2].data[i] = this.totaldata[i].midCharge; //
        this.option2.series[3].data[i] = this.totaldata[i].topCharge; //

        i = i + 1;
      }
    },
    // 
    handleCommand(command) {
      if (command == "a") this.getPdf();
      if (command == "b") this.getPdf1();
    },
    //
    convert() {
      //
      let parms3 = this.$qs.stringify({
        multipleId:'1',
        dataTime:'1638547200',
        dayNum:'31',
        type:'0',
      });
      
      axios
        .post("http://114.213.211.51/powerStatistics/findMultipleDevicePowerMsgForMouth", parms3)
        .then((response) => {
          console.log(2);
          this.tableData1 = response.data;
          console.log(this.tableData1);
        })
        .catch((error) => {
          console.log(error);
        });
      axios
        .post(
          "http://121.43.111.195/powerRealtime/findPowerDistributionDayByDataTime",
          {
            deviceId: "1",
            dataTime: "1638288000",
          }
        )
        .then((response) => {
          this.totaldata = response.data;
          //console.log(this.totaldata.length)
          console.log(this.totaldata);
          this.charge();
        })
        .catch((error) => {
          console.log(error);
        });
      axios
        .post(
          "http://121.43.111.195/PowerAnalyse/findPowerAnalyseDayAvgByDataTime",
          {
            deviceId: "1",
            dataTime: "1638288000",
          }
        )
        .then((response) => {
          this.TABLEdata = response.data;
          console.log(this.TABLEdata);

          this.sanxiang();
          this.xiebo();
          this.gonglv();
          this.piancha();
        })
        .catch((error) => {
          console.log(error);
        });
    },
    //
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          sums[index] += " ";
        } else {
          sums[index] = "N/A";
        }
      });

      return sums;
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    //echart
    open() {
      console.log("open");
      this.draw();
      this.draw1();
    },
    draw() {
      // echarts
      let myChart = this.$echarts.init(document.getElementById("myChart"));

      //canvas
      window.onresize = myChart.resize;
      myChart.setOption(this.option1); //option
    },

    draw1() {
      // echarts
      let myChart1 = this.$echarts.init(document.getElementById("myChart1"));
      // 

      //canvas
      window.onresize = myChart1.resize;
      myChart1.setOption(this.option2); //option
    },
  },
};
